import React from 'react';
import PropTypes from 'prop-types';
import BlockerIcon from '../assets/Icons/blocker.svg';
import STIcon from '../assets/Icons/st.svg';
import GSIcon from '../assets/Icons/gs.svg';

const ICONS = {
    '&blo': { img: BlockerIcon, text: 'ブロッカー' },
    '&st': { img: STIcon, text: 'S・トリガー' },
    '&gs': { img: GSIcon, text: 'G・ストライク' },
};

const TextWithIconsSVG = ({ text, x, y, fontSize }) => {
    // フォントサイズのデフォルト値を設定
    const computedFontSize = fontSize || 8.4;
    const lineHeight = computedFontSize * 1.2;

    // 特定の文字を統一されたバージョンに置換
    let modifiedText = text;
    modifiedText = modifiedText.replace(/▪/g, "■");
    modifiedText = modifiedText.replace(/█/g, "■");
    modifiedText = modifiedText.replace(/◾️/g, "■");

    // &がついていない全角の句読点を半角に置換
    modifiedText = modifiedText.replace(/(?<!&)。/g, "｡");
    modifiedText = modifiedText.replace(/&。/g, "。");
    modifiedText = modifiedText.replace(/(?<!&)、/g, "､");
    modifiedText = modifiedText.replace(/&、/g, "、");
    modifiedText = modifiedText.replace(/(?<!&)・/g, "･");
    modifiedText = modifiedText.replace(/&・/g, "・");
    modifiedText = modifiedText.replace(/「/g, "｢");
    modifiedText = modifiedText.replace(/」/g, "｣");

    // 改行を考慮してテキストを分割
    const words = modifiedText.split(/(<br>|\n)/g);

    let currentX = x;
    let currentY = y;
    const elements = [];

    words.forEach((word, index) => {
        // 改行処理
        if (word === '<br>' || word === '\n') {
            currentY += lineHeight;
            currentX = x; // 改行時にX座標をリセット
            return;
        }

        // アイコンと通常のテキストを分割
        const parts = word.split(/(&\w+);?/g);

        parts.forEach((part, idx) => {
            if (!part) return;

            const baselineY = currentY + computedFontSize;

            if (ICONS[part]) {
                const iconData = ICONS[part];

                // アイコン画像を描画
                elements.push(
                    <image
                        key={`${index}-${idx}-icon`}
                        href={iconData.img}
                        x={currentX}
                        y={baselineY - computedFontSize + 1}
                        width={computedFontSize}
                        height={computedFontSize}
                    />
                );
                currentX += computedFontSize + 1; // アイコンの幅とスペースを考慮してX座標を調整

                // キーワードテキストを描画
                elements.push(
                    <text
                        key={`${index}-${idx}-text`}
                        x={currentX}
                        y={baselineY}
                        fontSize={computedFontSize}
                        fill="white"
                        stroke="black"
                        strokeWidth="0.5"
                        fontWeight="bold"
                        fontFamily="LineSeedJP"
                    >
                        {iconData.text}
                    </text>
                );
                const textWidth = iconData.text.length * computedFontSize * 0.7;
                currentX += textWidth * 1.5; // テキストの幅とスペースを考慮してX座標を調整
            } else {
                // 通常テキストを描画
                const textWidth = part.length * computedFontSize * 0.6;

                elements.push(
                    <text
                        key={`${index}-${idx}-text`}
                        x={currentX}
                        y={baselineY}
                        fontSize={computedFontSize}
                        fill="white"
                        stroke="black"
                        strokeWidth="0.4"
                        fontWeight="bold"
                        fontFamily="LineSeedJP"
                    >
                        {part}
                    </text>
                );
                currentX += textWidth + 2; // テキストの幅とスペースを考慮してX座標を調整
            }
        });
    });

    return <g>{elements}</g>;
};

// PropTypesを追加してプロパティの型を明示
TextWithIconsSVG.propTypes = {
    text: PropTypes.string.isRequired, // 描画するテキスト
    x: PropTypes.number.isRequired, // X座標
    y: PropTypes.number.isRequired, // Y座標
    fontSize: PropTypes.number, // フォントサイズ（任意）
};

export default TextWithIconsSVG;