import React from 'react';
import PropTypes from 'prop-types';
import { getColor } from '../utils/getColor';
import CostNumberSVG from './CostNumberSVG';

const CostCircleSVG = ({ x, y, cost, civilizations }) => {
    // civilizations が undefined の場合、空配列を使用
    const validCivilizations = Array.isArray(civilizations) ? civilizations : [];

    // ゼロ文明を除外するが、ゼロ文明が唯一の場合は含める
    const filteredCivilizations = validCivilizations.filter(civ => civ !== 'ゼロ');
    const finalCivilizations = filteredCivilizations.length > 0 ? filteredCivilizations : validCivilizations;

    const circleRadius = 8; // 円の半径
    const circleCircumference = 2 * Math.PI * circleRadius; // 円周の長さ

    // 文明に応じて線のパターンを生成
    const getStrokeDashArray = () => {
        const civCount = finalCivilizations.length;
        const dashLength = circleCircumference / civCount; // 各文明に割り当てられる線の長さ
        return `${dashLength} ${circleCircumference - dashLength}`;
    };

    // 文明ごとのセグメントを生成
    const getCivilizationSegments = () => {
        const civCount = finalCivilizations.length;
        const dashLength = circleCircumference / civCount;

        // 文明の数が偶数なら45度回転、奇数なら-90度
        const rotationAngle = civCount % 2 === 0 ? 45 : -90;

        return finalCivilizations.map((civ, index) => {
            const offset = -index * dashLength; // 各文明のオフセットを計算
            return (
                <circle
                    key={civ}
                    cx="17" // 中心X座標
                    cy="17" // 中心Y座標
                    r={circleRadius} // 半径
                    fill="none" // 塗りつぶしなし
                    stroke={getColor(civ)} // 文明の色
                    strokeWidth="16" // 円の太さ（縁取りを見せるために黒い円より小さく）
                    strokeDasharray={getStrokeDashArray()} // 文明ごとに線を等分
                    strokeDashoffset={offset} // 文明の開始位置
                    transform={`rotate(${rotationAngle} 17 17)`} // 45度または-90度に回転
                />
            );
        });
    };

    return (
        <g transform={`translate(${x}, ${y})`}>
            {/* 黒い縁取りの円 */}
            <circle
                cx="17"
                cy="17"
                r={circleRadius}
                fill="black"
                stroke="black"
                strokeWidth="18" // 円の直径と同じにして円全体をカバー
            />
            {/* 文明ごとのセグメントを描画 */}
            {getCivilizationSegments()}
            {/* コストの数字 */}
            <CostNumberSVG number={cost} x={17} y={17} isCost />
        </g>
    );
};

// PropTypesを追加してプロパティの型を明示
CostCircleSVG.propTypes = {
    x: PropTypes.number.isRequired, // X座標
    y: PropTypes.number.isRequired, // Y座標
    cost: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // コストの数値
    civilizations: PropTypes.arrayOf(PropTypes.string), // 文明の配列（任意）
};

export default CostCircleSVG;