import React, { memo } from 'react';
import PropTypes from 'prop-types';

const CardNameSVG = ({
    x,
    y,
    cardName,
    rubyText,
    race,
    cardNameFontSize = 16,
    rubyTextFontSize = 8,
    raceFontSize = 7,
    cardNamePosition = { x: 0, y: 15.6 },
    rubyTextPosition = { x: 0, y: 0 },
    racePosition = { x: 0, y: 25 },
    alignment = 'center',
}) => {
    // テキストの配置を決定する
    const textAnchor = {
        left: 'start',
        center: 'middle',
        right: 'end',
    }[alignment];

    return (
        // カード名、ルビ、種族をまとめて描画するグループ要素
        <g transform={`translate(${x}, ${y})`}>
            {/* ルビテキストの描画 */}
            {rubyText && (
                <TextElement
                    text={rubyText}
                    position={rubyTextPosition}
                    fontSize={rubyTextFontSize}
                    textAnchor={textAnchor}
                    strokeWidth={0.35}
                />
            )}
            {/* カード名の描画 */}
            <TextElement
                text={cardName}
                position={cardNamePosition}
                fontSize={cardNameFontSize}
                textAnchor={textAnchor}
                strokeWidth={0.7}
            />
            {/* 種族の描画 */}
            {race && (
                <TextElement
                    text={race}
                    position={racePosition}
                    fontSize={raceFontSize}
                    textAnchor={textAnchor}
                    strokeWidth={0.5}
                />
            )}
        </g>
    );
};

const TextElement = memo(({ text, position, fontSize, textAnchor, strokeWidth }) => (
    <text
        x={position.x}
        y={position.y}
        fontSize={fontSize}
        fontWeight="bold"
        fill="white"
        stroke="black"
        strokeWidth={strokeWidth}
        fontFamily="LineSeedJP"
        textAnchor={textAnchor}
    >
        {/* テキストを分割して、スペースの処理を行う */}
        {text.split('').map((char, index) => (
            <tspan key={index} dx={char === ' ' ? fontSize * 0.05 : 0}>
                {char === ' ' ? ' ' : char}
            </tspan>
        ))}
    </text>
));

CardNameSVG.propTypes = {
    x: PropTypes.number.isRequired, // X座標
    y: PropTypes.number.isRequired, // Y座標
    cardName: PropTypes.string.isRequired, // カード名
    rubyText: PropTypes.string, // ルビテキスト（任意）
    race: PropTypes.string, // 種族（任意）
    cardNameFontSize: PropTypes.number, // カード名のフォントサイズ
    rubyTextFontSize: PropTypes.number, // ルビテキストのフォントサイズ
    raceFontSize: PropTypes.number, // 種族のフォントサイズ
    cardNamePosition: PropTypes.shape({
        x: PropTypes.number, // カード名のX座標
        y: PropTypes.number, // カード名のY座標
    }),
    rubyTextPosition: PropTypes.shape({
        x: PropTypes.number, // ルビテキストのX座標
        y: PropTypes.number, // ルビテキストのY座標
    }),
    racePosition: PropTypes.shape({
        x: PropTypes.number, // 種族のX座標
        y: PropTypes.number, // 種族のY座標
    }),
    alignment: PropTypes.oneOf(['left', 'center', 'right']), // テキストの配置（左、中央、右）
};

TextElement.propTypes = {
    text: PropTypes.string.isRequired, // 描画するテキスト
    position: PropTypes.shape({
        x: PropTypes.number, // テキストのX座標
        y: PropTypes.number, // テキストのY座標
    }).isRequired,
    fontSize: PropTypes.number.isRequired, // フォントサイズ
    textAnchor: PropTypes.string.isRequired, // テキストのアンカー位置（配置）
    strokeWidth: PropTypes.number.isRequired, // テキストのストローク幅
};

export default memo(CardNameSVG);