import React from 'react';
import PropTypes from 'prop-types';
import { getColor } from '../utils/getColor';

const CardTypeFrameSVG = ({ x, y, cardType, civilizations }) => {
    // civilizationsがundefinedの場合、空の配列を使用する
    const validCivilizations = Array.isArray(civilizations) ? civilizations : [];

    // ゼロ文明を除外するが、ゼロ文明が唯一の場合は含める
    const filteredCivilizations = validCivilizations.filter(civ => civ !== 'ゼロ');
    const finalCivilizations = filteredCivilizations.length > 0 ? filteredCivilizations : validCivilizations;

    // カードタイプが進化タイプかどうかを判定する
    const isEvolutionType = ['進化クリーチャー', '進化V', '進化GV', '無限進化', '究極進化'].includes(cardType);
    const displayType = isEvolutionType ? '進化クリーチャー' : cardType;

    // カードタイプテキストの長さに基づいてフレームの幅を計算する
    const textLength = displayType.length;
    const width = textLength * 8.4 + 20;

    // 文明に基づいて一意のグラデーションIDを作成する
    const gradientId = `card-type-gradient-${finalCivilizations.join('-')}`;

    // 文明に基づいてカードフレームのグラデーションストップを生成する関数
    const getGradientStops = () => {
        const civCount = finalCivilizations.length;
        const stops = [];

        // 文明が存在する場合、グラデーションストップを作成する
        if (civCount > 0) {
            // 最初の色を0%に追加
            stops.push(
                <stop
                    key={`stop-0`}
                    offset="0%"
                    stopColor={getColor(finalCivilizations[0])}
                />
            );

            // 文明をループしてグラデーションの遷移を作成する
            for (let i = 1; i < civCount; i++) {
                const offset = (i / civCount) * 100;

                // 現在のオフセットで前の色を追加
                stops.push(
                    <stop
                        key={`stop-${i}-prev`}
                        offset={`${offset}%`}
                        stopColor={getColor(finalCivilizations[i - 1])}
                    />
                );

                // 同じオフセットで現在の色を追加
                stops.push(
                    <stop
                        key={`stop-${i}-curr`}
                        offset={`${offset}%`}
                        stopColor={getColor(finalCivilizations[i])}
                    />
                );
            }

            // 最後の色を100%に追加
            stops.push(
                <stop
                    key={`stop-end`}
                    offset="100%"
                    stopColor={getColor(finalCivilizations[civCount - 1])}
                />
            );
        }

        return stops;
    };

    return (
        // カードタイプフレームを配置するためのグループ要素
        <g transform={`translate(${x}, ${y})`}>
            <defs>
                {/* カードフレームの線形グラデーションを定義する */}
                <linearGradient
                    id={gradientId}
                    gradientTransform="rotate(-6)"
                    spreadMethod="pad"
                >
                    {getGradientStops()}
                </linearGradient>
            </defs>
            {/* カードタイプフレームを表す矩形 */}
            <rect
                x="0"
                y="0"
                width={width}
                height="15"
                fill={validCivilizations.length > 0 ? `url(#${gradientId})` : 'none'}
                stroke="black"
                strokeWidth="1"
                rx="7.5"
            />
            {/* カードタイプを表示するテキスト */}
            <text
                x={width / 2}
                y="11"
                fontSize="8"
                fontWeight="bold"
                fill="black"
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {displayType}
            </text>
        </g>
    );
};

// PropTypesを追加してプロパティの型を明示
CardTypeFrameSVG.propTypes = {
    x: PropTypes.number.isRequired, // X座標
    y: PropTypes.number.isRequired, // Y座標
    cardType: PropTypes.string.isRequired, // カードタイプ
    civilizations: PropTypes.arrayOf(PropTypes.string) // 文明の配列
};

export default CardTypeFrameSVG;