import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import { Button } from '@mui/material';

const ImageUploadAndCrop = ({ onImageUpload }) => {
    const [image, setImage] = useState(null);
    const cropperRef = useRef(null);

    // 画像ファイルが選択されたときの処理
    const handleFileChange = (e) => {
        const files = e.dataTransfer ? e.dataTransfer.files : e.target.files;
        if (files && files.length > 0) {
            const reader = new FileReader();
            reader.onload = () => setImage(reader.result); // 画像を読み込み、状態に保存
            reader.readAsDataURL(files[0]);
        }
    };

    // トリミングされた画像を取得する処理
    const handleCropImage = () => {
        const cropper = cropperRef.current?.cropper;
        if (cropper) {
            const croppedImage = cropper.getCroppedCanvas().toDataURL(); // トリミングされた画像をData URLとして取得
            onImageUpload(croppedImage); // 取得した画像をコールバック関数に渡す
        }
    };

    return (
        <div>
            {/* 画像ファイルを選択する入力フィールド */}
            <input type="file" onChange={handleFileChange} accept="image/*" />
            {image && (
                <div>
                    {/* 画像のトリミングを行うCropperコンポーネント */}
                    <Cropper
                        ref={cropperRef}
                        src={image}
                        style={{ height: 400, width: '100%' }}
                        aspectRatio={252 / 344} // カードの比率に合わせる
                        guides={false}
                        cropBoxResizable
                        viewMode={1}
                    />
                    {/* トリミングした画像を取得するボタン */}
                    <Button variant="contained" color="primary" onClick={handleCropImage}>
                        画像をトリミング
                    </Button>
                </div>
            )}
        </div>
    );
};

ImageUploadAndCrop.propTypes = {
    onImageUpload: PropTypes.func.isRequired, // トリミングされた画像をアップロードするコールバック関数
};

export default ImageUploadAndCrop;