// CivilizationCheckboxGroup.js
import React from 'react';
import { Checkbox, FormControlLabel, FormGroup } from '@mui/material';

const CivilizationCheckboxGroup = ({ civilizations, selectedCivilizations, onChange }) => (
    // 文明のチェックボックスグループを表示するフォームグループ
    <FormGroup row>
        {civilizations.map((civ) => (
            // 各文明のチェックボックスとラベルを描画
            <FormControlLabel
                key={civ.value}
                control={
                    <Checkbox
                        value={civ.value} // 文明の値を設定
                        checked={selectedCivilizations.includes(civ.value)} // 選択されているかどうかを判定
                        onChange={onChange} // チェックボックスの状態が変わったときの処理
                    />
                }
                label={civ.label} // 文明のラベルを表示
            />
        ))}
    </FormGroup>
);

export default CivilizationCheckboxGroup;