import React from 'react';
import PropTypes from 'prop-types';
import EvoFramePart from '../assets/parts/evo.svg';
import EvoGrayFramePart from '../assets/parts/evoGray.svg';
import EvoUltraFramePart from '../assets/parts/evoUltra.svg';
import EvoStarFramePart from '../assets/parts/evoStar.svg';
import EvoStarMAXFramePart from '../assets/parts/evoStarMax.svg';

const FramePartsSVG = ({ cardType, startY, spacing }) => {
    // 各進化タイプに対応するデータを定義
    const evolutionTypes = {
        '進化クリーチャー': { count: 1, img: EvoFramePart },
        '進化V': { count: 2, img: EvoFramePart },
        '進化GV': { count: 3, img: EvoFramePart },
        '無限進化': { count: 4, img: EvoFramePart, lastImg: EvoGrayFramePart, narrowSpacing: true },
        '究極進化': { count: 1, img: EvoUltraFramePart },
        'スター進化クリーチャー': { count: 1, img: EvoStarFramePart },
        'S-MAX進化クリーチャー': { count: 1, img: EvoStarMAXFramePart }
    };

    // カードタイプに応じた進化データを取得
    const evoData = evolutionTypes[cardType];

    // 該当する進化データがない場合は何も描画しない
    if (!evoData) return null;

    const { count, img, lastImg, narrowSpacing } = evoData;
    const elements = [];

    // 進化パーツを順番に描画
    for (let i = count; i > 0; i--) {
        const isLast = i === count && lastImg; // 最後の進化パーツかどうかを判定
        const yPosition = startY - i * (narrowSpacing ? spacing * 0.3 : spacing); // 無限進化の場合はパーツ同士の間隔を狭める

        elements.push(
            <image
                key={i}
                href={isLast ? lastImg : img} // 最後のパーツの場合はlastImgを使用
                x={5}
                y={yPosition}
                width={30}
                height={30}
            />
        );
    }

    return <g>{elements}</g>; // グループ要素として進化パーツを描画
};

// PropTypesを追加してプロパティの型を明示
FramePartsSVG.propTypes = {
    cardType: PropTypes.string.isRequired, // カードの種類
    startY: PropTypes.number.isRequired, // 描画開始のY座標
    spacing: PropTypes.number.isRequired, // 各パーツ間のスペーシング
};

export default FramePartsSVG;