import React from 'react';
import PropTypes from 'prop-types';
import CardNameSVG from './CardNameSVG';
import CardTypeFrameSVG from './CardTypeFrameSVG';
import FramePartsSVG from './FramePartsSVG';
import CostCircleSVG from './CostCircleSVG';
import PowerNumberSVG from './PowerNumberSVG';
import CardTextSVG from './CardTextSVG';
import ManaCircleSVG from './ManaCircleSVG';
import frameImage from '../assets/parts/twinImpactFrame.svg'; // ツインパクトカード用のフレーム画像

const TwinImpactCardFrameSVG = ({
    creatureImageUrl,
    spellImageUrl,
    creatureData,
    spellData,
}) => {
    return (
        <svg width="252" height="344" viewBox="0 0 252 344" xmlns="http://www.w3.org/2000/svg">
            {/* クリーチャー画像部分 */}
            {creatureImageUrl && (
                <image href={creatureImageUrl} x="0" y="0" width="252" height="344" />
            )}

            {/* 呪文画像部分 */}
            {spellImageUrl && (
                <g transform="translate(126, 257) rotate(-10)">
                    <image href={spellImageUrl} x="-134" y="-86" width="270" height="172" className="spell-image" />
                    {/* 呪文画像の上下に黒い線を追加 */}
                    <rect x="-134" y="-79" width="270" height="5" fill="black" /> {/* 上辺の黒い線 */}
                    <rect x="-134" y="74.5" width="270" height="5" fill="black" />  {/* 下辺の黒い線 */}
                </g>
            )}

            {/* クリーチャー部分 */}
            {/* カード名（クリーチャー）を表示 */}
            <CardNameSVG
                x={126}
                y={24}
                cardName={creatureData.cardName}
                rubyText={creatureData.rubyText}
                race={creatureData.race}
                cardNameFontSize={creatureData.cardNameFontSize}
                rubyTextFontSize={creatureData.rubyTextFontSize}
                raceFontSize={creatureData.raceFontSize}
                cardNamePosition={creatureData.cardNamePosition}
                rubyTextPosition={creatureData.rubyTextPosition}
                racePosition={creatureData.racePosition}
                alignment={creatureData.alignment}
            />

            {/* カードタイプフレーム（クリーチャー）を表示 */}
            <CardTypeFrameSVG
                x={8.5}
                y={100}
                cardType={creatureData.cardType}
                civilizations={creatureData.civilizations}
            />

            {/* フレームパーツ（クリーチャー）を表示 */}
            <FramePartsSVG
                cardType={creatureData.cardType}
                startY={creatureData.evoMarkStartY}
                spacing={creatureData.evoMarkSpacing}
            />

            {/* 効果テキスト（クリーチャー）を表示 */}
            <CardTextSVG
                x={20}
                y={118}
                effectText={creatureData.effectText}
                flavorText={creatureData.flavorText}
                effectTextFontSize={creatureData.effectTextFontSize}
                flavorTextFontSize={creatureData.flavorTextFontSize}
            />

            {/* 呪文部分 */}
            {/* カード名（呪文）を表示 */}
            <g transform="rotate(-10 126 200)">
                <CardNameSVG
                    x={126}
                    y={194}
                    cardName={spellData.cardName}
                    rubyText={spellData.rubyText}
                    race={spellData.race}
                    cardNameFontSize={spellData.cardNameFontSize}
                    rubyTextFontSize={spellData.rubyTextFontSize}
                    raceFontSize={spellData.raceFontSize}
                    cardNamePosition={spellData.cardNamePosition}
                    rubyTextPosition={spellData.rubyTextPosition}
                    racePosition={spellData.racePosition}
                    alignment={spellData.alignment}
                />
            </g>

            {/* フレームパーツ（呪文）を表示 */}
            <FramePartsSVG
                cardType={spellData.cardType}
                startY={spellData.evoMarkStartY}
                spacing={spellData.evoMarkSpacing}
            />

            {/* カードタイプフレーム（呪文）を表示 */}
            <CardTypeFrameSVG
                x={8.5}
                y={245}
                cardType={spellData.cardType}
                civilizations={spellData.civilizations}
            />

            {/* 効果テキスト（呪文）を表示 */}
            <CardTextSVG
                x={20}
                y={264}
                effectText={spellData.effectText}
                flavorText={spellData.flavorText}
                effectTextFontSize={spellData.effectTextFontSize}
                flavorTextFontSize={spellData.flavorTextFontSize}
            />

            {/* カードフレーム画像を表示 */}
            <image href={frameImage} x="0" y="0" width="252" height="344" />

            {/* カードフレームより上に置く要素 */}
            {/* コストサークル（クリーチャー）を表示 */}
            <CostCircleSVG x={7} y={7} cost={creatureData.cost} civilizations={creatureData.civilizations} />
            {/* パワー（クリーチャー）を表示 */}
            <PowerNumberSVG number={creatureData.power} x={20} y={328} />

            {/* コストサークル（呪文）を表示 */}
            <CostCircleSVG x={214} y={152} cost={spellData.cost} civilizations={spellData.civilizations} />

            {/* マナサークルを表示 */}
            <ManaCircleSVG
                x={108.3}
                y={308}
                creatureCivilizations={creatureData.civilizations}
                spellCivilizations={spellData.civilizations}
            />
        </svg>
    );
};

// PropTypesを追加してプロパティの型を明示
TwinImpactCardFrameSVG.propTypes = {
    creatureImageUrl: PropTypes.string, // クリーチャー画像のURL
    spellImageUrl: PropTypes.string, // 呪文画像のURL
    creatureData: PropTypes.shape({
        cardName: PropTypes.string.isRequired, // カード名
        rubyText: PropTypes.string, // ルビテキスト
        race: PropTypes.string, // 種族
        cardNameFontSize: PropTypes.number, // カード名のフォントサイズ
        rubyTextFontSize: PropTypes.number, // ルビテキストのフォントサイズ
        raceFontSize: PropTypes.number, // 種族のフォントサイズ
        cardNamePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // カード名の位置
        rubyTextPosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // ルビテキストの位置
        racePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // 種族の位置
        alignment: PropTypes.oneOf(['left', 'center', 'right']), // カード名のアライメント
        cardType: PropTypes.string.isRequired, // カードタイプ
        civilizations: PropTypes.arrayOf(PropTypes.string).isRequired, // 文明の配列
        evoMarkStartY: PropTypes.number, // 進化マークの開始位置Y
        evoMarkSpacing: PropTypes.number, // 進化マーク間のスペース
        effectText: PropTypes.string, // 効果テキスト
        flavorText: PropTypes.string, // フレーバーテキスト
        effectTextFontSize: PropTypes.number, // 効果テキストのフォントサイズ
        flavorTextFontSize: PropTypes.number, // フレーバーテキストのフォントサイズ
        cost: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // コスト
        power: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // パワー
    }).isRequired,
    spellData: PropTypes.shape({
        cardName: PropTypes.string.isRequired, // カード名
        rubyText: PropTypes.string, // ルビテキスト
        race: PropTypes.string, // 種族
        cardNameFontSize: PropTypes.number, // カード名のフォントサイズ
        rubyTextFontSize: PropTypes.number, // ルビテキストのフォントサイズ
        raceFontSize: PropTypes.number, // 種族のフォントサイズ
        cardNamePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // カード名の位置
        rubyTextPosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // ルビテキストの位置
        racePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // 種族の位置
        alignment: PropTypes.oneOf(['left', 'center', 'right']), // カード名のアライメント
        cardType: PropTypes.string.isRequired, // カードタイプ
        civilizations: PropTypes.arrayOf(PropTypes.string).isRequired, // 文明の配列
        evoMarkStartY: PropTypes.number, // 進化マークの開始位置Y
        evoMarkSpacing: PropTypes.number, // 進化マーク間のスペース
        effectText: PropTypes.string, // 効果テキスト
        flavorText: PropTypes.string, // フレーバーテキスト
        effectTextFontSize: PropTypes.number, // 効果テキストのフォントサイズ
        flavorTextFontSize: PropTypes.number, // フレーバーテキストのフォントサイズ
        cost: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // コスト
    }).isRequired,
};

export default TwinImpactCardFrameSVG;